<template>
	<div id="operation">
	 <div class="mainNavadd">
      <el-button type="primary" @click="addRow()"></el-button>
    </div>
    <div class="mainSearch">
      <el-input class="mainSearchFloat" v-model="mainSearchinput" placeholder=""></el-input>
      <el-button class="mainSearchFloat" type="primary" @click="mainSearch()" v-loading.fullscreen.lock="fullscreenLoading"></el-button>
    </div>

    <div class="mainNavaddB" v-show="mainAddShow">
      <div class="mainContentadd" v-show="mainContentaddC"><!---->
      <p class="Updatetitle"></p>
      <hr color="#E5E9F2" width="90%">
        <el-form ref="ruleOperation" :rules="ruleOperation" :model="form" label-width="100px" label-position="right">
          <el-form-item label="" prop="operationName">
            <el-input class="main-add-input" v-model="form.operationName"></el-input>
          </el-form-item>
          <el-form-item label="URL">
              <el-input class="main-add-input" v-model="form.operationUrl"></el-input>
            </el-form-item>
          <el-form-item label="">
            <el-select class="main-add-input" v-model="moduleId" placeholder="">
                <el-option v-for="item in module" :key="item.id" :label="item.moduleName" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item class="description" label="">
            <el-input type="textarea" class="main-add-input" v-model="form.description"></el-input>
          </el-form-item>

          <el-form-item class="samllButton">
            <el-button type="primary" @click="onSubmit('ruleOperation')"></el-button>
            <el-button @click="onCancel('ruleOperation')"></el-button>
          </el-form-item>

        </el-form>
      </div>
      <div class="mainContentview" v-show="mainContentviewC">
        <p class="Updatetitle"></p>
        <hr color="#E5E9F2" width="90%">
          <el-form ref="ruleOperation" :rules="ruleOperation" :model="form" label-width="100px" label-position="right">
            <el-form-item label="ID">
              <el-input class="main-add-input" v-model="form.id" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="" prop="operationName">
              <el-input class="main-add-input" v-model="form.operationName"></el-input>
            </el-form-item>
            <el-form-item label="URL">
              <el-input class="main-add-input" v-model="form.operationUrl"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select class="main-add-input" v-model="moduleId" placeholder="">
                <el-option v-for="item in module" :key="item.id" :label="item.moduleName" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item class="description" label="">
              <el-input type="textarea" class="main-add-input" v-model="form.description"></el-input>
            </el-form-item>
            <el-form-item class="samllButton">
              <el-button type="primary" @click="onSubmitU('ruleOperation')"></el-button>
              <el-button @click="onCancelU('ruleOperation')"></el-button>
            </el-form-item>

          </el-form>      
      </div>
      <div class="toviewC" v-show="toviewC"> 
          <p class="Updatetitle"></p>
        <hr color="#E5E9F2" width="90%">
          <el-form ref="form" class="viewPadding"  label-width="100px">
            <el-form-item class="floatLeft" label="ID">
              <el-input class="main-add-input" v-model="form.id" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item class="floatLeft" label="">
              <el-input class="main-add-input" v-model="form.operationName" :disabled="true"></el-input>
            </el-form-item>
            <div class="clear"></div>
            <el-form-item class="floatLeft" label="URL">
              <el-input class="main-add-input" v-model="form.operationUrl" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item class="floatLeft" label="">
              <el-input class="main-add-input" v-model="form.moduleName" :disabled="true"></el-input>
            </el-form-item>
            <div class="clear"></div>
            <!--<el-form-item class="floatLeft" label="">
              <el-input class="main-add-input" v-model="form.createBy" :disabled="true"></el-input>
            </el-form-item>
            <div class="clear"></div>
            <el-form-item class="floatLeft" label="">
              <el-input class="main-add-input" v-model="form.createTime" :disabled="true"></el-input>
            </el-form-item>-->
            <el-form-item class="floatLeft" label="">
              <el-input class="main-add-input" v-model="form.updateBy" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item class="floatLeft" label="">
              <el-input class="main-add-input" v-model="form.updateTime" :disabled="true"></el-input>
            </el-form-item>
            <div class="clear"></div>
            <el-form-item class="viewDescription" label="">
              <el-input type="textarea" class="main-add-input" v-model="form.description" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item class="cancelButton">
              <el-button @click="onCancelUU()"></el-button>
            </el-form-item>

          </el-form>      
      </div>
    </div>


    <el-table :data="tableData" style="width: 100%" border tooltip-effect="dark">
    </el-table-column>
      <el-table-column type="index" label="" width="65px"></el-table-column>
      <el-table-column prop="moduleName" label=""></el-table-column>
      <el-table-column prop="operationName" label=""></el-table-column>
      <el-table-column label="">
         <template scope="scope">
            <el-button @click.native.prevent="toView(scope.$index,tableData)" type="text" size="small"></el-button>
            <el-button @click.native.prevent="changeRow(scope.$index,tableData)" type="text" size="small"></el-button>
            <el-button @click.native.prevent="deleteRow(scope.$index,tableData)" type="text" size="small" v-loading.fullscreen.lock="fullscreenLoading"></el-button>
          </template>  
       </el-table-column>
    </el-table>
    <div class="block">
      <el-pagination v-bind:current-Page="pageIndex" v-bind:page-size="pageSize" :total="total"
        layout="sizes,prev,pager,next,jumper" v-bind:page-sizes="pageSizes"
        v-on:size-change="sizeChange" v-on:current-change="pageIndexChange"></el-pagination>
  </div>
  </div>
</template>
<script>
export default{
  data(){
    return{
      tableData:[],
      multipleSelection: [],      //
      form:{},
      mainContentviewV:'',
      pageIndex:1,
      pageSize:10,
      total:1,
      pageSizes:[1,10,20,50,100],
      mainSearchinput:'',
      mainAddShow:false,
      mainContentaddC:false,
      mainContentviewC:false,
      toviewC:false,
      fullscreenLoading: false,
      module:[],
      moduleId:'',
      ruleOperation:{
        operationName:[
        { required: true, message: '', trigger: 'blur' }
        ]
      },
    }
  },
  created:function(){
    this.getUsers();
  },
  methods:{
    getUsers(){
      this.$http.get(this.$store.state.api+'/operation/',{params:{pageNum:this.pageIndex,pageSize:this.pageSize}},{emulateJSON: true})
      .then((res)=>{
        this.tableData = res.body.dataList;
        this.total = res.body.objectotalNumber; 
      })
    },
    changeRow:function(index){      //
      this.mainAddShow = !this.mainAddShow;
      this.mainContentviewC = !this.mainContentviewC;
      this.selectmodule();
      this.$http.get(this.$store.state.api+'/operation/id',{params:{id:this.tableData[index].id}},{emulateJSON: true})
      .then((res)=>{
        this.form = res.body.data;
        this.moduleId = res.body.data.moduleId;
      })
    },
    deleteRow:function(index){      //
      this.fullscreenLoading = true;
        setTimeout(() => {
          this.fullscreenLoading = false;
          this.$http.delete(this.$store.state.api+'/operation/id',{params:{id:this.tableData[index].id}},{emulateJSON: true})
      .then((res)=>{
        this.getUsers();
      })
      }, 1000);
    },
    toView:function(index){       //
      this.mainAddShow = !this.mainAddShow;
      this.toviewC = !this.toviewC;
      this.$http.get(this.$store.state.api+'/operation/id',{params:{id:this.tableData[index].id}},{emulateJSON: true})
      .then((res)=>{
        this.form = res.body.data;
        this.moduleId = res.body.data.moduleId;
      })
    },
    addRow:function(){          //
      this.mainAddShow = !this.mainAddShow;
      this.mainContentaddC = !this.mainContentaddC;
      this.selectmodule();
    },
    onSubmit:function(formName){  //
      this.form.description='';
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http.post(this.$store.state.api+'/operation/insert',{moduleId:this.moduleId,operationName:this.form.operationName,operationUrl:this.form.operationUrl,description:this.form.description})
          .then((res)=>{
            this.getUsers();
          });
          this.onCancel(formName);
        } else {  
            this.open();
            return false;
          }
      });
    },
    open() {
      this.$message('');
    },
    onSubmitU:function(formName){
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http.post(this.$store.state.api+'/operation/update',{id:this.form.id,moduleId:this.moduleId,operationName:this.form.operationName,operationUrl:this.form.operationUrl,description:this.form.description})
          .then((res)=>{
            this.getUsers();
          });
          this.onCancelU(formName);
        } else {  
            this.open();
            return false;
          }
      });
    },
    onCancel:function(formName){        //
      this.form.operationName='';
      this.form.description='';
      this.moduleId='';
      this.module='';
      this.form.operationUrl='';
      this.mainAddShow = !this.mainAddShow;
      this.mainContentaddC = !this.mainContentaddC;
      this.$refs[formName].resetFields();
    },
    selectmodule:function(){
      this.$http.get(this.$store.state.api+'/constant/module')
      .then((res)=>{
        this.module = res.body;
      });
    },
    onCancelU:function(formName){
      this.form.operationName='';
      this.form.description='';
      this.moduleId='';
      this.module='';
      this.form.operationUrl='';
      this.mainAddShow = !this.mainAddShow;
      this.mainContentviewC = !this.mainContentviewC;
      this.$refs[formName].resetFields();
    },
    onCancelUU:function(){
      this.form.operationName='';
      this.form.description='';
      this.moduleId='';
      this.module='';
      this.form.operationUrl='';
      this.mainAddShow = !this.mainAddShow;
      this.toviewC = !this.toviewC;
    },
    sizeChange: function (pageSize) {   //
      this.pageSize = pageSize;
      this.fetchData();
    },
    pageIndexChange: function (pageIndex) {   //
      this.pageIndex = pageIndex;
      this.fetchData();
    },
    fetchData: function(){
      this.$http.get(this.$store.state.api+'/operation/',{params:{pageNum:this.pageIndex,pageSize:this.pageSize}},{emulateJSON: true})
      .then((res)=>{
        this.tableData = res.body.dataList;
        })
    },
    mainSearch:function(){    //
      this.fullscreenLoading = true;
        setTimeout(() => {
          this.fullscreenLoading = false;
          this.$http.get(this.$store.state.api+'/operation/',{params:{pageNum:1,pageSize:this.pageSize,operationName:this.mainSearchinput}},{emulateJSON: true})
          .then((res)=>{
          this.tableData = res.body.dataList;
          })
        }, 1000);
    }
  }
}
</script>

<style>
#operation {
  overflow: hidden;
  padding-left: 10px;
}

.mainNavaddB {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 99;
}

#operation .toviewC {
  width: 650px;
  height: 500px;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  border-radius: 10px;
}

.mainContentadd {
  width: 400px;
  height: 380px;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  border-radius: 10px;
}

#operation .mainContentview {
  width: 400px;
  height: 420px;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  border-radius: 10px;
}

.cancelButton {
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 15px;
  margin-left: -100px;
}

.floatLeft {
  float: left;
}

.toviewC .viewPadding {
  margin-top: 20px;
}

#operation .description {
  width: 400px !important;
}

.main-add-input-B {
  width: 70% !important;
}

#selectContents .el-form-item__content {
  margin-left: 73px !important;
}

.viewDescription {
  width: 700px !important;
}

#operation .samllButton {
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 15px;
  margin-left: -100px;
}
</style>


